import React,{useState, useEffect} from 'react'
import axios from 'axios'
import { Link, useHistory,useParams } from "react-router-dom";
import Cookies from "js-cookie";
import Alert from './middlewares/Alert'
import Spinner from './middlewares/Spinner';
export default function ResetPassword() {
    let { code } = useParams();
    let history = useHistory();

    const [errors, setErrors] = useState(null)
    const [success, setSuccess] = useState(null)
    const [isLoading, setIsLoading] = useState(false)
    const [showPassword, setShowPassword] = useState(false)
    const [user, setUser] = useState({});
    const [formData, setFormData] = useState({
        password: "",
        confirmpassword: "",
      })

      const handleSubmit = async e => {
        e.preventDefault();
        setIsLoading(true)
        setErrors(null)
        setSuccess(null)
        try{
          const resp = await axios.post(`/auth/reset-password/${code}`, formData);
          setIsLoading(false);
          const {errors, success} = resp.data;
          if(errors){ setErrors(errors); return;}
          setSuccess([{msg:`Your password has been changed successfully, Kindly log in to continue`}]);
        }catch(err){
          setErrors([
            {
              msg: "An error occured with the server, Try again."
            }
          ])
          setIsLoading(false)
        }
    
      }

      const handleChange = e => {
        e.preventDefault();
        setErrors(null)
        setFormData({
          ...formData,
          [e.target.name]:e.target.value
        })
      }

      const getUserData = async () => {
        try {
          const userDetails = await axios.get(`/auth/reset-password/${code}`);
          setIsLoading(false);
          let { errors, success } = userDetails.data;
          if (errors) {
            setIsLoading(false);
            setErrors(errors);
            return;
          } 
            //Check if link is expired
            const expiry = new Date(success[0].resetDetails.expiry);
            if (new Date() > expiry) {
                setIsLoading(false);
              setErrors([
                {msg: "This link is expired, click 'Forgot Password' to get a new link "
                }
              ]);
              return;
            }
            setUser(success[0].resetDetails);
        } catch (err) {
            setIsLoading(false);
          setErrors([
            { msg: "An error occured with the server, Reload this page " }
          ]);
          return;
        }
      };
    
      useEffect(() => {
        //get user details
        getUserData();
      }, []);
    return (
        <div className="main-page-wrapper user-access-page">


        <div className="main-container">
            <div className="inner-box">
                <div className="logo">
                    <Link to="/" style={{display:"flex", justifyContent:"center"}}>
                    <img style={{width:"50%"}} src={process.env.REACT_APP_LOGO_URL} alt=""/>
                    </Link>
                    </div>
                    <h4 class="main-title">Reset Password </h4>
                <form onSubmit={handleSubmit} action="#" className="login-form" autocomplete="off">
                {success ? (<>
              <Alert type="success">
                {success[0].msg}
              </Alert>
              <Link to="/login" className="pb-3">Click here to login</Link>
              </>) : ""}
            {errors ? (
              <Alert type="danger">
                {errors[0].msg  + (errors[0].param? ": "+errors[0].param.toUpperCase():"")}
              </Alert>) : ""}
                   
                    
                    <div style={{position:"relative"}}>
                    <input onChange={handleChange} type={showPassword ? "text" : "password"}  name="password" value={formData.password} placeholder="New Password"/>
                   
                </div>

                    <div style={{position:"relative"}}>
                    <input onChange={handleChange} type={showPassword ? "text" : "password"}  name="confirmpassword" value={formData.confirmpassword} placeholder="Retype Password"/>
                   
                </div>

                    <button  style={{display:"flex", alignItems:"center",justifyContent:"center"}}  type="submit" className="theme-button"><span></span>{isLoading?<Spinner/>:"Reset Password"}</button>
                </form>
            </div> 
        </div> 
        </div>
    )
}
