import React,{useState} from 'react'
import { Link, useHistory, useLocation } from "react-router-dom";
import axios from 'axios';
import Alert from './middlewares/Alert'
import Spinner from './middlewares/Spinner';

export default function VerifyEmail() {
    let location = useLocation();
    const [isLoading, setIsLoading] = useState(false)
    const [errors, setErrors] = useState(null)
    const [success, setSuccess] = useState(null)


    const resendLink = async ()=>{
        try{
            setIsLoading(true)
            const response  = await axios.get(`/auth/send-activation/${location.state.email}`);
            const {errors, success} = response.data;
            setIsLoading(false)
            if(errors){setErrors(errors); return;}
           if(success) setSuccess([{msg:`An activation link has been sent to ${location.state.email}`}]);
        }catch(err){
            setIsLoading(false)
            setErrors([
                {
                  msg: "An error occured with the server, Try again."
                }
              ])
        }
    }
    return (
        <div className="main-page-wrapper user-access-page">


        <div className="main-container">
            <div className="inner-box">
            <div className="logo">
                    <Link to="/" style={{display:"flex", justifyContent:"center"}}>
                    <img style={{width:"50%"}} src={process.env.REACT_APP_LOGO_URL} alt=""/>
                    </Link>
                    </div>
                <h4 className="main-title">Verify your Email</h4>
                {success ? (<>
              <Alert type="success">
                {success[0].msg}
              </Alert>
              </>) : ""}
                {errors ? (
              <Alert type="danger">
                {errors[0].msg  + (errors[0].param? ": "+errors[0].param.toUpperCase():"")}
              </Alert>) : ""}

    <p style={{fontSize:16}}>Your email is yet to be verified, Click on the verification link sent to <code>{location.state.email}</code></p>

                <div className="text-center" style={{display:"flex",justifyContent:"center"}}>
                <button onClick={resendLink} style={{backgroundColor:"transparent"}}  className="text-center forget-pass-link font-fix ">{isLoading?<Spinner type="dark"/>:"Resend Link"}</button>
            </div>
            </div>

            <div className="text-center">
            <Link to="/login" className="additional-link-text font-fix">Back to Login</Link>
              </div> 
        </div>
        </div>
    )
}
