import React,{useState, useEffect} from 'react'
import axios from 'axios'
import { Link, useHistory } from "react-router-dom";
import Cookies from "js-cookie";
import Alert from './middlewares/Alert'
import Spinner from './middlewares/Spinner';

export default function Register() {
    let history = useHistory();
    const [isLoading, setIsLoading] = useState(false)
    const [errors, setErrors] = useState(null)
    const [showPassword, setShowPassword] = useState(false)
    const [formData, setFormData] = useState({
      name: "",
      email: "",
      phone: "",
      password: ""
    })

    const handleChange = e => {
        e.preventDefault();
        setErrors(null)
        setFormData({
          ...formData,
          [e.target.name]:e.target.value
        })
      }

      const handleSubmit = async e => {
        e.preventDefault();
        setIsLoading(true)
        setErrors(null)
        try{
          const resp = await axios.post("/auth/register", formData);
          setIsLoading(false);
          const {errors, token} = resp.data;
          if(errors){ setErrors(errors); return;}
            // Cookies.set('TOKEN', token, {expires: 30});
            // window.location = "/feed"
            history.push({
              pathname: "/registration-successful",
              state:{email:formData.email}
            })
        }catch(err){
          setErrors([
            {
              msg: "An error occured with the server, Try again."
            }
          ])
          setIsLoading(false)
        }
    
      }


    return (
        <div className="main-page-wrapper user-access-page">


        <div className="main-container">
            <div className="inner-box">
            <div className="logo">
                    <Link to="/" style={{display:"flex", justifyContent:"center"}}>
                    <img style={{width:"50%"}} src={process.env.REACT_APP_LOGO_URL} alt=""/>
                    </Link>
                    </div>
                <h4 className="main-title">Create your account</h4>
                <form onSubmit={handleSubmit} action="#" className="sign-up-form" autoComplete="off">
                {errors ? (
              <Alert type="danger">
                {errors[0].msg  + (errors[0].param? ": "+errors[0].param.toUpperCase():"")}
              </Alert>) : ""}
                    <input required value={formData.name} onChange={handleChange} type="text" name="name" placeholder="Full Name"/>
                    <input required value={formData.email} onChange={handleChange} type="email" name="email" placeholder="Email"/>
                    <input required value={formData.phone} onChange={handleChange} id="phone" name="phone" type="text" placeholder="Phone"/>

                    
                    

                <div style={{position:"relative"}}>
                    <input required style={{marginBottom:0}} onChange={handleChange} type={showPassword ? "text" : "password"}  name="password" value={formData.password} placeholder="Password"/>
                    <i onClick={()=>setShowPassword(!showPassword)} className={showPassword ? "passwordRevealIcon fa fa-eye-slash" : "passwordRevealIcon fa fa-eye"}></i>
                </div>
                    <p style={{marginTop:5}} className="password-text">Your password should have at least 6 characters</p>
                    <button style={{display:"flex", alignItems:"center", justifyContent:"center"}} disabled={isLoading} className="theme-button"><span></span>{isLoading?<Spinner/>:"Create account"}</button>
                </form>
                <div className="condition-text font-fix">By clicking 'Create account' you agree to our<Link to="/TOS">Terms of service</Link> and <Link to="/privacy-policy">Privacy policy</Link>.</div>
            </div>

            <div className="text-center">
                <Link to="/login" className="additional-link-text font-fix">Already have an account?</Link>
            </div>
        </div>
        </div>
    )
}
