import React from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import AuthLayout from './components/layouts/AuthLayout';
import Login from './components/Login';
import Register from './components/Register';
import SuccessRegistration from './components/SuccessRegistration';
import Dashboard from './components/Dashboard';
import Logout from './components/Logout';
import ForgotPassword from './components/ForgotPassword';
import ResetPassword from './components/ResetPassword';
import "pace-progressbar"
import "pace-progressbar/themes/blue/pace-theme-flash.css"
import VerifyEmail from './components/VerifyEmail';
import AccountVerified from './components/AccountVerified';
const App = () => {
    return (
      <Router>
      <Switch>
      <Route path="/" exact>
          <AuthLayout><Login/></AuthLayout>
      </Route>
      <Route path="/login" exact>
          <AuthLayout><Login/></AuthLayout>
      </Route>
      <Route path="/register" exact>  <AuthLayout><Register/></AuthLayout>  </Route>
      <Route path="/forgot-password" exact>  <AuthLayout><ForgotPassword/></AuthLayout>  </Route>
      <Route path="/reset-password/:code" exact> <AuthLayout><ResetPassword/></AuthLayout>  </Route>
      <Route path="/registration-successful" exact> <AuthLayout><SuccessRegistration/></AuthLayout>  </Route>
      <Route path="/activation/:code" exact> <AuthLayout><AccountVerified/></AuthLayout>  </Route>
      <Route path="/verify-email" exact> <AuthLayout><VerifyEmail/></AuthLayout>  </Route>
      <Route path="/dashboard" exact>  <AuthLayout><Dashboard/> </AuthLayout></Route>
      <Route path="/logout"> <Logout/> </Route>

      </Switch>
      </Router>
    )
}
export default App;
