import React,{useEffect, useState} from 'react'
import { useParams, useHistory, Link} from "react-router-dom";
import axios from 'axios'
import Cookies from "js-cookie";
import Alert from './middlewares/Alert';


export default function AccountVerified() {
    const {code} = useParams();
    const history = useHistory();

    const [isVerified, setIsVerified] = useState(false)
    const [errors, setErrors] = useState(false)

    const getToken = async () => {
        try{
            const response = await axios.get(`/auth/activate-account/${code}`);
            const {errors, token} = response.data;
            if(errors){
                setErrors(errors);return;
            }
            Cookies.set('TOKEN', token, {expires: 30});
            setIsVerified(true)
        }catch(err){
            setErrors([ { msg: "An error occured with the server, Try again." } ])
        }
    }

    useEffect(() => {
       getToken()
    }, [])
    useEffect(() => {
       if(isVerified) history.push("/dashboard")
    }, [isVerified])
    return (
        <div class="main-page-wrapper user-access-page">
        <div class="main-container">
            <div class="inner-box">
            <div className="logo">
                    <Link to="/" style={{display:"flex", justifyContent:"center"}}>
                    <img style={{width:"50%"}} src={process.env.REACT_APP_LOGO_URL} alt=""/>
                    </Link>
                    </div>
                    {errors ? (
              <Alert type="danger">
                {errors[0].msg  + (errors[0].param? ": "+errors[0].param.toUpperCase():"")}
              </Alert>) : ""}

                <h4 class="main-title">{isVerified?"Account Verified!":"Verifying Account"}</h4>
    <p style={{fontSize:16}}>{isVerified?"Your account has been verified, you will be redirected shortly":"Please wait..."}</p>

            </div>
        </div>
        </div>
    )
}
