import React,{useState, useEffect} from 'react'
import axios from 'axios'
import { Link, useHistory } from "react-router-dom";
import Cookies from "js-cookie";
import Alert from './middlewares/Alert'
import Spinner from './middlewares/Spinner';

export default function ForgotPassword() {
    let history = useHistory();
    const [errors, setErrors] = useState(null)
    const [isLoading, setIsLoading] = useState(false)
    const [mailSent, setMailSent] = useState(false)
    const [email, setEmail] = useState("")

      const handleSubmit = async e => {
        e.preventDefault();
        setIsLoading(true)
        setErrors(null)
        try{
          const resp = await axios.post("/auth/forgot-password", {email});
          const {errors, success} = resp.data;
          if(errors){ setIsLoading(false);
            setErrors(errors); return;}
           if(success) setMailSent(true)
        }catch(err){
          setErrors([
            {
              msg: "An error occured with the server, Try again."
            }
          ])
          setIsLoading(false)
        }
    
      }
      useEffect(() => {
        if (Cookies.get("TOKEN") !== null) {
          let token = Cookies.get("TOKEN");
          if (typeof token !== "undefined" && token !== null) {
            history.push("/dashboard");
          }
        }
      }, []);
    return (
        <div className="main-page-wrapper user-access-page">


        <div className="main-container">
            <div className="inner-box">
                <div className="logo">
                    <Link to="/" style={{display:"flex", justifyContent:"center"}}>
                    <img style={{width:"50%"}} src={process.env.REACT_APP_LOGO_URL} alt=""/>
                    </Link>
                    </div>
                    <h4 class="main-title">Forgot Password? </h4>
                { !mailSent?<>
                    <p>Enter your email below to reset your password</p>
                <form onSubmit={handleSubmit} action="#" className="login-form" autocomplete="off">
                {errors ? (
              <Alert type="danger">
                {errors[0].msg  + (errors[0].param? ": "+errors[0].param.toUpperCase():"")}
              </Alert>) : ""}
                    <input onChange={(e)=>setEmail(e.target.value)} value={email} type="email" name="email" placeholder="Email"/>
                    
                    <button style={{display:"flex", alignItems:"center",justifyContent:"center"}} className="theme-button"><span></span>{isLoading?<Spinner/>:"Send"}</button>
                </form>
                </>:
                <div style={{display:"flex",flexDirection:"column", alignItems:"center", justifyContent:"center", paddingBottom:55}}>
                    <i className="checkmark fa fa-envelope" style={{marginBottom:5}}></i>
                    <p style={{textAlign:"center", fontSize:16}}>
                        A reset link has been sent to <code>{email}</code>. click on the link to reset your password
                    </p>
                </div>
                }
            </div> 

            <div className="text-center">
                    <Link to="/login" className="additional-link-text font-fix">Login Instead!</Link>
                </div>
        </div> 
        </div>
    )
}
