import React,{useState, useEffect} from 'react'
import axios from 'axios'
import { Link, useHistory, useLocation } from "react-router-dom";
import Cookies from "js-cookie";
import Alert from './middlewares/Alert'
import Spinner from './middlewares/Spinner';

export default function Login() {
    let history = useHistory();
    const [errors, setErrors] = useState(null)
    const [isLoading, setIsLoading] = useState(false)
    const [showPassword, setShowPassword] = useState(false)
    const [formData, setFormData] = useState({
        email: "",
        password: ""
      })

      const handleChange = e => {
        e.preventDefault();
        setErrors(null)
        setFormData({
          ...formData,
          [e.target.name]:e.target.value
        })
      }

      const handleSubmit = async e => {
        e.preventDefault();
        setIsLoading(true)
        setErrors(null)
        try{
          const resp = await axios.post("/auth/login", formData);
          const {errors, token, unverified} = resp.data;
          if(errors){ setIsLoading(false);
            setErrors(errors); return;}
            if(unverified){
              history.push({
              pathname: "/verify-email",
              state:{email:formData.email}
          });
           return;
          }
            Cookies.set('TOKEN', token, {expires: 30});
            history.push("/dashboard")
        }catch(err){
          setErrors([
            {
              msg: "An error occured with the server, Try again."
            }
          ])
          setIsLoading(false)
        }
    
      }
      useEffect(() => {
        if (Cookies.get("TOKEN") !== null) {
          let token = Cookies.get("TOKEN");
          if (typeof token !== "undefined" && token !== null) {
            history.push("/dashboard");
          }
        }
      }, []);
    return (
        <div className="main-page-wrapper user-access-page">


        <div className="main-container">
            <div className="inner-box">
                <div className="logo">
                    <Link to="/" style={{display:"flex", justifyContent:"center"}}>
                    <img style={{width:"50%"}} src={process.env.REACT_APP_LOGO_URL} alt=""/>
                    </Link>
                    </div>
                    <h4 className="main-title">Log in </h4>
                <form onSubmit={handleSubmit} action="#" className="login-form" autoComplete="off">
                {errors ? (
              <Alert type="danger">
                {errors[0].msg  + (errors[0].param? ": "+errors[0].param.toUpperCase():"")}
              </Alert>) : ""}
                    <input required onChange={handleChange} value={formData.email} type="email" name="email" placeholder="Email"/>
                    
                    <div style={{position:"relative"}}>
                    <input required style={{marginBottom:0}} onChange={handleChange} type={showPassword ? "text" : "password"}  name="password" value={formData.password} placeholder="Password"/>
                    <i onClick={()=>setShowPassword(!showPassword)} className={showPassword ? "passwordRevealIcon fa fa-eye-slash" : "passwordRevealIcon fa fa-eye"}></i>
                </div>
                    <button disabled={isLoading} style={{marginTop:25, display:"flex", alignItems:"center", justifyContent:"center"}} className="theme-button">{isLoading?<Spinner/>:"Login"} </button>
                </form>
                <div className="text-center">
                    <Link to="/forgot-password" className="forget-pass-link font-fix">Forgot your password?</Link>
                </div>
            </div> 

            <div className="text-center">
            <Link to="/register" className="additional-link-text font-fix">Create an account</Link>
              </div> 
        </div> 
        </div>
    )
}
